import {Link} from 'gatsby'
import React from 'react'

const Footer = () => (
    <div style={{width: '100%', maxWidth: '1024px', margin: '20px auto', textAlign: 'center', fontSize: '0.85rem', color: '#979797' }}>
        <div className="social_media" style={{ marginBottom: '10px' }}>
            <i className="fab fa-twitter"></i>
            <i className="fab fa-instagram"></i>
            <i className="fab fa-codepen"></i>
        </div>
        <Link to="/" replace style={{ background: '#FFCA71', color: '#ffffff', textDecoration: 'none', padding: '0 5px', margin: '0 5px' }}>Edwin Chen</Link>
        <span>— Powered by GatsbyJS</span>
    </div>
)

export default Footer